import { useDispatch, useSelector } from "react-redux";

import {
  setCAAllPlants,
  setCAHeaders,
  setCAItemDetails,
  setHeaders,
  setIsMFGCA,
  setLoading,
  setObselenceObjectData,
  setParentDetailsLoading,
  // setPlantObjectData,
  setProductChildren,
  setProposedChanges,
  setSpecDocument,
  setCAName,
  setCAData,
} from "../store/droppedObjectSlice";
import { MSG_FETCH_CSRF_HEADERS_FAILED } from "../utils/toastMessages";
import useToast from "./useToast";
import { fetchCsrfToken } from "../services/api/PlantAssignment/fetchCsrfService";
import { initWidget } from "../lib/widget";
import { callEnoviaWebService, loadWAFData, fetchData } from "../utils/helpers";
import { useCallback, useEffect } from "react";
import { getUserGroupCollab } from "../services/api/PlantAssignment/userGroupService";
import { getAllPlants } from "../services/api/PlantAssignment/allPlantSevice";
import {
  getAssignedClasses,
  getItemDetails,
  getLatestRevision,
  getScopedItms,
} from "../pages/plantAssignment/dataHelpers";
import { fetchAssignedPlants } from "../services/api/PlantAssignment/assignedPlantService";
import { fetchProductChildren } from "../services/api/PlantAssignment/plantChildrenService";
import { fetchCADetails } from "../services/api/PlantAssignment/CADetailService";

const useObsolescenceWidget = () => {
  const { showErrorToast } = useToast();
  const dispatch = useDispatch();
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;
  let email = window.widget.getValue("email");

  const handleObsolescenceWidget = useCallback(
    async (collabSpace, objectId, type, relativePath) => {
      if (!collabSpace || !objectId || !type || !relativePath) {
        console.error(
          "[handleObsolescenceWidget] âŒ Missing required argument(s):",
          { collabSpace, objectId, type, relativePath }
        );
        return;
      }

      try {
        dispatch(setObselenceObjectData({ parentObjectData: [] }));
        let tableOfArrayData = [];
        dispatch(setParentDetailsLoading(true));

        const headers = await fetchCsrfToken();
        if (!headers) {
          showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
          return;
        }

        // Locate API call helper
        let ChildObject = await locateApi(
          ENOVIA_BASE_URL,
          objectId,
          type,
          relativePath,
          headers
        );
        if (ChildObject.status && ChildObject.output) {
          let member =
            ChildObject?.output?.member[0]["dseng:EngInstance"]?.member;
          if (member?.length > 0) {
            let url = [];
            member.forEach((items) => {
              url.push(
                items.parentObject?.source + items.parentObject?.relativePath
              );
            });

            let revisionStateData = [];
            if (url.length > 0) {
              const promises = url.map(async (apiPath) => {
                const revisionState = await callEnoviaWebService(
                  "GET",
                  apiPath,
                  "",
                  headers
                );
                if (revisionState) {
                  revisionStateData = [
                    ...revisionStateData,
                    ...revisionState?.output?.member,
                  ];
                }
              });
              await Promise.all(promises);
            }

            dispatch(
              setObselenceObjectData({ parentObjectData: revisionStateData })
            );
          } else {
            console.warn("âš ï¸ No parent object members found in locate call.");
          }

          if (type === "Change Action") {
            let isMFGCA = false;
            let ItemDetails = [];
            const tableOfArrayData = [];

            const fetchChangeActionData = async () => {
              const CAURL = `${ENOVIA_BASE_URL}/enovia/resources/v1/modeler/dslc/changeaction/${objectId}?%24fields=proposedChanges,realizedChanges`;
              const response = await callEnoviaWebService(
                "GET",
                CAURL,
                "",
                headers
              );
              if (response.status && response.output) {
                for (
                  let i = 0;
                  i < response?.output?.proposedChanges?.length;
                  i++
                ) {
                  const change = response.output.proposedChanges[i];
                  const url = change.where.source + change.where.relativePath;
                  const responseofTable = await callEnoviaWebService(
                    "GET",
                    url,
                    "",
                    headers
                  );
                  if (responseofTable.status) {
                    tableOfArrayData.push({
                      ...responseofTable?.output?.member[0],
                      changeIntent: change?.whats,
                    });
                  }
                }
              } else {
                console.error("Invalid response structure or no proposed changes.");
              }
            };

            const CAAllPlantsData = async () => {
              const allPlants = await getAllPlants(
                [collabSpace],
                headers,
                objectId
              );
              dispatch(setCAAllPlants(allPlants));
            };

            await Promise.all([fetchChangeActionData(), CAAllPlantsData()]);

            dispatch(setParentDetailsLoading(false));
            dispatch(setCAItemDetails(ItemDetails));
            dispatch(setIsMFGCA(isMFGCA));
            dispatch(setCAHeaders(headers));
            dispatch(
              setObselenceObjectData({ parentObjectData: tableOfArrayData })
            );
          } else {
            const userGroupCollab = await getUserGroupCollab(
              headers,
              objectId,
              email
            );

            let allCollabSpaces = [...userGroupCollab, collabSpace];
            let allPlants = [];
            if (allCollabSpaces.length > 0) {
              allPlants = await getAllPlants(allCollabSpaces, headers, objectId);
            }

            if (allPlants.length > 0) {
              const plants = await fetchAssignedPlants(allPlants, headers, objectId);
              if (plants.success) {
                dispatch(setObselenceObjectData(plants.data.plantData));
                dispatch(setHeaders(headers));
              } else {
                console.error("Failed to fetch plant data.");
              }
            }

            let getProductChildren = {};
            if (type === "Raw_Material") {
              getProductChildren = { success: true, data: [] };
            } else {
              getProductChildren = await fetchProductChildren(
                headers,
                objectId,
                type
              );
            }

            if (getProductChildren.success) {
              dispatch(setProductChildren(getProductChildren.data));
            }

            const state = null;
            const getCaDetails = await fetchCADetails(headers, objectId, state);
            if (getCaDetails.success) {
              dispatch(setCAName(getCaDetails.data));
              dispatch(setProposedChanges(getCaDetails.proposedChanges));
              dispatch(setCAData(getCaDetails.CAData));
            }
          }
        }
      } catch (error) {
        console.error("[useObsolescenceWidget] Error:", error);
        throw error;
      } finally {
        dispatch(setParentDetailsLoading(false));
      }
    },
    [dispatch, ENOVIA_BASE_URL, email, showErrorToast]
  );

  const obsoleteStateChecking = async (tableData, droppedObjectData) => {
    if (!tableData) {
      showErrorToast("Items Not Found");
      return;
    }
    let itemGroupId = droppedObjectData?.initialDraggedData?.data;
    let objectId = itemGroupId?.items[0]?.objectId;
    try {
      dispatch(setLoading(true));
      const headers = await fetchCsrfToken();
      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return;
      }
      let locateParentObjectsArray = [];
      let finalObsoleteStatusArray = [];
      for (let i = 0; i < tableData.length; i++) {
        const item = tableData[i];
        let locateApiResponse = await locateApi(
          ENOVIA_BASE_URL,
          item.id,
          item.type,
          `/resources/v1/modeler/dseng/dseng:EngItem/${item.id}`,
          headers
        );
        if (locateApiResponse?.status) {
          const engInstances = locateApiResponse?.output?.member[0] || [];
          const locateParentObject =
            engInstances["dseng:EngInstance"]?.member || null;
          locateParentObjectsArray.push(locateParentObject);
        }
        if (locateParentObjectsArray) {
          for (let j = 0; j < locateParentObjectsArray.length; j++) {
            const parentMembers = locateParentObjectsArray[j];
            if (parentMembers.length > 0) {
              await Promise.all(
                parentMembers.map(async (item) => {
                  let revisionState = await callEnoviaWebService(
                    "GET",
                    `${item?.parentObject?.source}${item?.parentObject?.relativePath}`,
                    "",
                    headers
                  );
                  if (revisionState?.status) {
                    const obsoleteStateData = revisionState?.output?.member[0];
                    finalObsoleteStatusArray.push(obsoleteStateData);
                  }
                })
              );
            }
          }
        }
      }
      const hasObsolete = finalObsoleteStatusArray.every(
        (item) => item.state === "OBSOLETE"
      );
      if (hasObsolete) {
        if (droppedObjectData?.cardData["Maturity State"] === "In Work") {
          await stateChange(
            objectId,
            "In Approval",
            ENOVIA_BASE_URL,
            headers,
            showErrorToast
          );
        } else if (droppedObjectData?.cardData["Maturity State"] === "Draft") {
          const response1 = await stateChange(
            objectId,
            "In Work",
            ENOVIA_BASE_URL,
            headers,
            showErrorToast
          );
          if (response1?.success) {
            await stateChange(
              objectId,
              "In Approval",
              ENOVIA_BASE_URL,
              headers,
              showErrorToast
            );
          }
        }
      } else {
        showErrorToast("All Parent Items Should be Obsolete");
      }
    } catch (err) {
      showErrorToast("Error in Obsolence");
    } finally {
      dispatch(setLoading(false));
    }
  };


  // Approve Change Action
  const approveChangeAction = async (
    ENOVIA_BASE_URL,
    objectId,
    cestamp,
    comment,
    headers
  ) => {
    const url = `${ENOVIA_BASE_URL}/enovia/resources/v1/modeler/dscm/changeaction/${objectId}/approve`;
    const body = {
      cestamp,
      comment,
    };

    try {
      const response = await callEnoviaWebService("PUT", url, body, headers);
      if (response.status) {
        console.log("Change Action approved successfully");
        return { success: true, data: response };
      } else {
        const errorMessage = response.errorMessage || "Approval failed";
        console.error(errorMessage);
        return { success: false, error: errorMessage };
      }
    } catch (error) {
      console.error("Error approving Change Action:", error);
      return { success: false, error: error.message };
    }
  };

  // Exposed function to approve CA given objectId and optional comment
  const approveCA = async (objectId, comment = "Approved Successfully") => {
    try {
      const headers = await fetchCsrfToken();
      if (!headers) {
        showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);
        return { success: false, error: "Missing CSRF headers" };
      }

      const caDetailsResponse = await fetchCADetails(headers, objectId, null);
      if (!caDetailsResponse.success || !caDetailsResponse.data) {
        showErrorToast("Failed to fetch Change Action details");
        return { success: false, error: "Failed to fetch CA details" };
      }

      const cestamp = caDetailsResponse.data.cestamp;
      if (!cestamp) {
        showErrorToast("Cestamp not found for approval");
        return { success: false, error: "Cestamp not found" };
      }

      const approveResponse = await approveChangeAction(
        ENOVIA_BASE_URL,
        objectId,
        cestamp,
        comment,
        headers
      );

      if (!approveResponse.success) {
        showErrorToast(approveResponse.error || "Failed to approve Change Action");
        return approveResponse;
      }

      return approveResponse;
    } catch (error) {
      console.error("Error in approveCA:", error);
      showErrorToast("Error approving Change Action");
      return { success: false, error: error.message };
    }
  };

  return {
    handleObsolescenceWidget,
    obsoleteStateChecking,
    approveCA,
  };
};

export default useObsolescenceWidget;

// Helper function locateApi
async function locateApi(ENOVIA_BASE_URL, objectId, type, relativePath, headers) {
  const url = `${ENOVIA_BASE_URL}${relativePath}`;
  try {
    const response = await callEnoviaWebService("GET", url, "", headers);
    return response;
  } catch (error) {
    console.error("Error in locateApi:", error);
    return { status: false, error };
  }
}

// Helper function stateChange (assumed signature)
async function stateChange(objectId, targetState, ENOVIA_BASE_URL, headers, showErrorToast) {
  try {
    const url = `${ENOVIA_BASE_URL}/enovia/resources/v1/modeler/dscm/changeaction/${objectId}/state`;
    const body = { state: targetState };
    const response = await callEnoviaWebService("PUT", url, body, headers);
    if (response.status) {
      return { success: true };
    } else {
      showErrorToast("Failed to change state");
      return { success: false };
    }
  } catch (error) {
    showErrorToast("Error changing state");
    return { success: false };
  }
}
